}
.icon-size {
    width: 60px;
    height: 60px;
}
ins {
    text-underline-offset: 6px;
    -webkit-text-decoration: underline 3px;
            text-decoration: underline 3px;
}
.home-link {
    color: black;
}

.home-link:focus {
    text-decoration: none;
    outline: none;
}

.home-link:hover {
    color: black;
    text-decoration: none;
}

.side-bar-full-title {
    padding-left: 50px;
