    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: rgb(119, 51, 207);
    font-weight: bold;
    font-size: 19px;
    padding-left: 15px;
    -webkit-text-decoration: underline 3px;
            text-decoration: underline 3px;
