}
.avatar-content {
    display: flex;
}
.alarm-content {
    margin-right: 35px;    
    cursor: pointer;
}
.avatar-title {
    display: flex;
    flex-direction: column;
    text-align: right;
    color: black;
    font-size: 18px;
    font-weight: 600;
    align-self: center;
    line-height: 24px;
}
.badge:empty {
    display:inherit!important;
}
.on-off {
    font-size: 10px;
}
.on-off-icon {
    height: 15px;
    position: relative;
    left: 12px;
    bottom: -40px;
}
.image-position{
    position: relative;
    left: 25px;
}
.sidebar-name {
    padding-left: 25px;
    text-decoration: none;
}

.route-navigate-icon {
    width: 30px;
    height: 30px;
    display: block;
}

.navigation-icons-name {
    margin: 0px;
    font-size: 18px;
    font-family: regularText;
    color: #333333;
    padding-left: 10px;
}

.navigation-icons-row {
    /* width: 35%; */
    text-decoration: none;
    /* float: right; */
    /* margin-right: 880px; */
}

@media only screen and (max-width: 1500px) {
    .side-bar {
        width: 7%;
    }

    .navigation-icons-name {
        display: none;
    }

    .navigation-icons-container {
        width: 100%;
    }

    .navigation-icons-row {
        width: auto;
    }

    .side-bar-full-title {
        display: none;
    }

    .side-bar-reduced-title {
        display: flex;
    }
}

@media only screen and (max-width: 1335px) {
    .side-bar {
        width: 100vw;
        height: 125px;
        justify-content: flex-start;
    }
    
