        display: flex;
        margin-top: 10px;
    }

    .side-bar-reduced-title {
        display: none;
    }

    .navigation-icons-container {
        display: flex;
        flex-direction: row;
        padding-top: 0px;
        width: 50%;
        justify-content: space-evenly;
    }

